.success-con {
  width: 100%;
  min-height: 100%;
}
.success-con.listen-con {
  background: rgba(128, 242, 183, 0.2);
}
.success-con.vip-con {
  background: rgba(254, 238, 192, 0.5);
}
.success-con.normal-con {
  background: rgba(221, 228, 246, 0.5);
}
.success-con.vvip-con {
  background: #333333;
}
.success-con .header {
  text-align: center;
}
.success-con .header img {
  width: 1.6rem;
  margin: 0.4rem auto 0.2rem;
}
.success-con .success-detail {
  width: 3.45rem;
  margin: 0 auto;
  text-align: center;
  padding: 0.2rem 0;
}
.success-con .success-detail.listen_bg {
  background: url(../../static/media/listen-bg.ba2fe668.png) no-repeat left center;
  background-size: 100% 100%;
}
.success-con .success-detail.listen_bg .company {
  color: #0E5630;
  margin-bottom: 0.22rem;
}
.success-con .success-detail.listen_bg a {
  background: #105d34;
}
.success-con .success-detail.listen_bg .time {
  width: 1.03rem;
  height: 0.2rem;
  font-size: 0.14rem;
  font-weight: 500;
  color: #666c8a;
  line-height: 0.2rem;
}
.success-con .success-detail.normal_bg {
  background: url(../../static/media/normal-bg.6c5ffd99.png) no-repeat left center;
  background-size: 100% 100%;
}
.success-con .success-detail.normal_bg .company {
  color: #666C8A;
  margin-bottom: 0.22rem;
}
.success-con .success-detail.normal_bg a {
  background: #666c8a;
}
.success-con .success-detail.normal_bg .time {
  width: 1.03rem;
  height: 0.2rem;
  font-size: 0.14rem;
  font-weight: 500;
  color: #666c8a;
  line-height: 0.2rem;
}
.success-con .success-detail.vip_bg {
  background: url(../../static/media/vip-bg.79f5c452.png) no-repeat left center;
  background-size: 100% 100%;
}
.success-con .success-detail.vip_bg .company {
  color: #AB762A;
  margin-bottom: 0.22rem;
}
.success-con .success-detail.vip_bg a {
  background: #ab762a;
}
.success-con .success-detail.vip_bg .time {
  width: 1.03rem;
  height: 0.2rem;
  font-size: 0.14rem;
  font-weight: 500;
  color: #ab762a;
  line-height: 0.2rem;
}
.success-con .success-detail.vvip_bg {
  background: url(../../static/media/vvip-bg.b7022c77.png) no-repeat left center;
  background-size: 100% 100%;
}
.success-con .success-detail.vvip_bg .company {
  color: #4F3106;
  margin-bottom: 0.22rem;
}
.success-con .success-detail.vvip_bg a {
  background: #493f43;
}
.success-con .success-detail.vvip_bg .time {
  width: 1.03rem;
  height: 0.2rem;
  font-size: 0.14rem;
  font-weight: 500;
  color: #4F3106;
  line-height: 0.2rem;
}
.success-con .success-detail .con-logo {
  width: 0.46rem;
  margin-bottom: 0.1rem;
}
.success-con .success-detail h2 {
  width: 1.56rem;
  height: 0.24rem;
  font-size: 0.18rem;
  font-family: PingFangSC-Medium, PingFangSC;
  font-weight: 500;
  color: #333333;
  line-height: 0.24rem;
  margin: 0 auto;
  letter-spacing: 0.02rem;
  margin-bottom: 0.2rem;
}
.success-con .success-detail .vip {
  width: 0.78rem;
}
.success-con .success-detail .name {
  height: 0.9rem;
  font-size: 0.7rem;
  font-weight: 500;
  color: #000000;
  line-height: 0.9rem;
  text-align: center;
}
.success-con .success-detail .time {
  margin: 0.6rem auto 0.2rem;
}
.success-con .success-detail a {
  width: 3.15rem;
  height: 0.44rem;
  line-height: 0.44rem;
  border-radius: 0.06rem;
  margin: 0 auto;
  color: #fff;
  font-size: 0.2rem;
}
.success-con .success-detail a::before {
  border: none;
}
.fail-con {
  width: 100%;
  height: 100%;
  background: #EEEEEE;
  padding: 0.2rem 0;
}
.fail-con .fail-detail {
  width: 3.45rem;
  border-radius: 0.06rem;
  background: -webkit-linear-gradient(226deg, #f14142 0%, #d63437 100%);
  background: linear-gradient(224deg, #f14142 0%, #d63437 100%);
  margin: 0 auto;
  text-align: center;
  padding: 0.2rem 0;
  box-shadow: 0px -2px 3px 0px rgba(0, 0, 0, 0.05);
}
.fail-con .fail-detail .con-logo {
  width: 1.28rem;
  margin-bottom: 0.23rem;
}
.fail-con .fail-detail h2 {
  margin-bottom: 0.2rem;
}
.fail-con .fail-detail h2 img {
  width: 1.8rem;
  border-bottom: 1px solid rgba(255, 255, 255, 0.4);
  padding-bottom: 0.3rem;
}
.fail-con .fail-detail .normal {
  margin-bottom: 0.2rem;
}
.fail-con .fail-detail .normal p.title {
  margin: 0 auto;
  width: 2.4rem;
  height: 0.6rem;
  font-size: 0.16rem;
  font-weight: 400;
  color: #ffffff;
  line-height: 0.3rem;
  margin-bottom: 0.2rem;
}
.fail-con .fail-detail .normal p.title2 {
  margin: 0 auto;
  width: 1.6rem;
  height: 0.6rem;
  font-size: 0.2rem;
  font-weight: 400;
  color: #ffffff;
  line-height: 0.3rem;
}
.fail-con .fail-detail .normal p.bott {
  margin: 0 auto;
  height: 0.24rem;
  font-size: 0.16rem;
  font-weight: 400;
  color: #ffffff;
  line-height: 0.24rem;
}
.fail-con .fail-detail .normal .reason {
  border: 0.01rem solid #fff;
  width: 2.8rem;
  margin: 0 auto;
  text-align: left;
  padding: 0 0.1rem;
  margin-bottom: 0.2rem;
}
.fail-con .fail-detail .normal .reason p {
  font-size: 0.12rem;
  font-weight: 400;
  color: #ffffff;
  line-height: 0.3rem;
  position: relative;
}
.fail-con .fail-detail .normal .reason p.tips {
  text-indent: 0.27rem;
}
.fail-con .fail-detail .normal .reason p.tips span {
  position: absolute;
  left: 0;
  top: 0.07rem;
  width: 0.16rem;
  height: 0.16rem;
  border-radius: 50%;
  background: #fff;
  color: #D63437;
  text-align: center;
  font-size: 0.12rem;
}
.fail-con .fail-detail .normal .reason p a {
  width: 0.76rem;
  height: 0.25rem;
  border-radius: 0.04rem;
  border: 0.01rem solid #ffffff;
  text-align: center;
  color: #fff;
  text-indent: 0;
  line-height: 0.25rem;
}
.fail-con .fail-detail .normal .apply {
  width: 3.1rem;
  height: 0.5rem;
  background: #ffffff;
  border-radius: 0.04rem;
  font-size: 0.16rem;
  font-family: PingFangSC-Medium, PingFangSC;
  font-weight: 500;
  color: #e1393b;
  line-height: 0.5rem;
}
.fail-con .fail-detail .normal .gold {
  color: #DEA943;
  font-size: 0.2rem;
  text-align: center;
}
.fail-con .fail-detail .normal .bind {
  display: block;
  width: 3.1rem;
  height: 0.5rem;
  border-radius: 0.04rem;
  background: #ffffff;
  line-height: 0.5rem;
  color: #e1393b;
  font-size: 0.16rem;
  text-align: center;
  margin: 0 auto;
}
.fail-con .fail-detail .normal .phone {
  width: 0.6rem;
  margin-bottom: 0.4rem;
}
.fail-con .fail-detail .erwei {
  width: 1.7rem;
  margin-top: 0.1rem;
}
.fail-con .fail-detail .failog {
  width: 1.25rem;
}
.sell-sign {
  width: 100%;
  background: #f2f3f3;
}
.sell-sign .header {
  width: 100%;
  height: 2.1rem;
  background: url(/static/media/bg.bb133b17.png) no-repeat 0;
  background-size: 100%;
  text-align: center;
}
.sell-sign .header img {
  width: 1.6rem;
  margin: 0.5rem auto;
}
.sell-sign .sell-sign-con {
  width: 3.45rem;
  border-radius: 0.06rem;
  background-color: #ffffff;
  position: relative;
  top: -0.8rem;
  margin: 0 auto;
  text-align: center;
  padding: 1.7rem 0;
  box-shadow: 0px -2px 3px 0px rgba(0, 0, 0, 0.05);
}
.sell-sign .sell-sign-con img {
  width: 0.8rem;
  margin-bottom: 0.5rem;
}
.sell-sign .sell-sign-con h2 {
  color: #333333;
  font-size: 0.18rem;
}
